@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'sass:color';

.contenedor-boletos {
  background-color: v.$verde;

  h3 {
    color: v.$blanco;
  }

  .boletos {
    @include m.contenedor;

    @include m.tablet {
      @include m.grid(2);
    }
  }
}

.pase {
  transition: transform 0.3s ease-in-out;

  &:hover {
    transform: scale(1.05);
  }

  &-nombre {
    font-size: 3.8rem;
    font-weight: 900;
    text-align: center;
    color: v.$blanco;
  }

  &-header,
  &-footer {

    p {
      text-align: center;
      color: v.$blanco;
      margin: 0;
    }
  }

  &-header {
    background-color: v.$rosa;

    p {
      text-transform: uppercase;
      font-size: 2.2rem;
      padding: 1rem 0;
    }
  }

  &-body {
    background-color: v.$blanco;

    ul {
      @include m.reset-lists;

      li {
        padding: 2rem;
        border-bottom: .1rem solid #e1e1e1;
      }
    }
  }

  &-footer {
    background-color: v.$blanco;
    padding: 3rem;

    p {
      font-size: 2.8rem;
      font-weight: 900;
      background-color: v.$morado;
      padding: 1rem 0;
      border-radius: 1rem;
      cursor: pointer;
      transition: 0.2s ease-in-out;

      &:hover {
        background-color: color.adjust(v.$morado, $lightness: -10%);
      }
    }
  }
}